  background-size: cover;
  background-repeat: no-repeat;
  background-size: 100%; */
}

body {
  margin: 0px !important;
  height: 100vh;
}

code {
  direction: ltr !important;
  line-height: 1.45;
  padding: 0;
  padding-left: 3 em;
  padding-top: 1 em;
  padding-bottom: 0.5 em;
  position: relative;
  overflow-x: auto;
  overflow-y: auto;
  font-family: "DejaVuSansMono", "Menlo", "Liberation Mono", "Consolas",
    "DejaVu Sans Mono", "Ubuntu Mono", "Courier New", "andale mono",
    "lucida console", monospace !important;
  white-space: pre;
  word-wrap: normal;
  padding: 3px 7px;
  color: #24292e;
  background-color: rgba(27, 31, 35, 0.05);
  border-radius: 3px;
  font-size: 1rem;
}

#root {
  height: 100vh;
}

.backButton {
  fill: #454f63 !important;
  max-height: 20px;
  max-width: 20px;
  stroke: #454f63;
  margin: 20px 0px 0px 20px;
  cursor: pointer;
}

.Contacts {
  border-radius: 3px;
  border-top: 1px solid #fbfbfb;
  box-shadow: 0 2px 2px 0 hsla(0, 0%, 0%, 0.2);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  margin-left: 1px;
  margin-right: 1px;
  margin-top: 25px;
  background: #fff;
}

.HelpButtonContainer {
  bottom: 150px;
  position: fixed;
  right: 18px;
  z-index: 100015;
}

.HelpButton {
  background-color: rgba(64, 138, 248, 0.7);
  border: 0;
  border-radius: 20px 20px 2px 20px;
  box-shadow: 0 5px 20px 0 rgba(12, 73, 84, 0.2);
  color: #fff;
  cursor: pointer;
  display: block;
  font-size: 12px;
  line-height: normal;
  padding: 17px 10px 16px;
  text-decoration: none;
}

.HelpButton:hover {
  background-color: rgba(64, 138, 248);
}

.HelpMenuContainer {
  background-color: #f9f9f9;
  border: none;
  bottom: 0;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
  height: 100%;
  max-width: 100%;
  opacity: 0;
  position: fixed;
  right: 0;
  transition: opacity 150ms, visibility 150ms, width 150ms;
  visibility: hidden;
  width: 0;
  z-index: 111100;
}

.HelpMenuContainerOpen {
  opacity: 1;
  overflow-y: auto;
  visibility: visible;
  width: 440px;
}

.HintToBottom {
  background: #383838;
  border-radius: 2px;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.3);
  color: #fff;
  font-size: 12px;
  line-height: 12px;
  padding: 8px 10px;
  z-index: 1000000;
  bottom: -15%;
  margin-bottom: -14px;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  right: -110%;
  transition: 0.3s ease;
  transition-delay: 0.1s;
  visibility: hidden;
  white-space: nowrap;
}
.hint-to-bottom:hover {
  opacity: 1;
  transform: translate(-50%, 8px);
  visibility: visible;
}

.SelectButton {
  width: 200px;
  background-color: #2595ad;
  border-color: #2595ad;
  border-radius: 5px;
  border-style: solid;
  border-width: 1px;
  border: none;
  box-shadow: 0px 2px 0 #2595ad;
  color: rgb(255, 255, 255);
  cursor: pointer;
  font-size: 14px;
  height: 32px;
  line-height: 16px;
  min-height: 45px;
  outline: none;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}

.SelectButton:hover {
  background-color: #115672;
  border-color: #115672;
}

.flex {
  display: flex;
}
.flexCol {
  display: flex;
  flex-direction: column;
}

.hljs {
  display: block;
  overflow-x: auto;
  background: #221a0f;
  color: #d3af86;
  padding: 0.5em;
}

.justAndAlignCenter {
  justify-content: center;
  align-items: center;
}

.Link {
  color: #1264a3;
  cursor: pointer;
  fill: #1264a3;
  font-size: 14px;
  font-weight: 500;
  line-height: 16px;
}

.Link:hover {
  text-decoration: underline !important;
}

.noTextDecoration {
  text-decoration: none !important;
}

.SvgSize {
  cursor: pointer;
  height: 500px;
  width: auto;
}

.mainHeader {
  font-size: 50px;
}

.muntraBackground {
  /* background-image: url(./assets/MuntraBackground.png); */
  background-color: #f6f9fc;
  background-size: cover;
  background-repeat: no-repeat;
  background-size: 100%;
  height: inherit;
  width: inherit;
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: 0px !important;
}

.HeadingStyle {
  color: #484848;
  font-weight: 700;
}

.UploadIconContainer {
  position: absolute;
  right: 0;
  top: 0;
}

.UploadIconContainer:hover {
  opacity: 1;
  visibility: visible;
}

.Uploadlabel {
  cursor: pointer;
  height: 39px;
  padding: 10px;
  width: 39px;
  background-color: transparent;
  background-image: linear-gradient(rgba(0, 0, 0, 0.07), rgba(0, 0, 0, 0.07));
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: 0% 0%;
  border: 0;
  border-radius: 50%;
  box-sizing: border-box !important;
  color: rgba(0, 0, 0, 0.54);
  display: flex;
  flex-direction: column;
  fill: rgba(0, 0, 0, 0.54);
  flex-shrink: 0;
  outline: none;
  overflow: hidden;
  position: relative;
  stroke: rgba(0, 0, 0, 0.54);
  text-align: center;
  transition: background-size 0.13s;
  vertical-align: top;
}

.Uploadlabel:hover {
  background-size: 100% 100%;
  fill: #000;
  stroke: #000;
}

@media (max-width: 992px) {
  .muntraBackground {
    height: auto;
    width: auto;
    overflow-x: hidden;
  }

  .mobileFlexCol {
    flex-direction: column;
    padding-bottom: 75px;
  }
}

html {
  color: #444;
  font-family: Arial, sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  display: flex;
  flex-direction: column;
  height: 100vh;
  margin: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
a {
  background-color: transparent;
}
a:active,
a:hover {
  outline: 0;
}
abbr[title] {
  border-bottom: 1px dotted;
}
b,
strong {
  font-weight: 700;
}
dfn {
  font-style: italic;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
h2 {
  margin: 2em 0 0 0;
}
mark {
  background: #ff0;
  color: #000;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
figure {
  margin: 1em 40px;
}
hr {
  box-sizing: content-box;
  height: 0;
}
pre {
  overflow: auto;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
button {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
input {
  line-height: normal;
}
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: content-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  border: 0;
  padding: 0;
}
textarea {
  overflow: auto;
}
optgroup {
  font-weight: 700;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
.hidden,
[hidden] {
  display: none !important;
}
.pure-img {
  max-width: 100%;
  height: auto;
  display: block;
}
.auto-margin {
  margin: auto;
}
.code {
  border: 1px solid #e1e4e8;
  border-radius: 6px;
  font-family: SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 12px;
  margin: 20px 0 10px 0;
  padding: 8px;
}
.flex-column {
  display: flex;
  flex-direction: column;
}
.flex-row {
  display: flex;
  flex-direction: row;
}
.muntra-widget-button {
  background-color: #52a8ff;
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
  border: 4px solid;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 100px;
  color: #ffffff;
  cursor: pointer;
  font-family: "Lato", Helvetica, Arial, Lucida, sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.7em;
  margin: auto;
  padding: 0.3em 1em;
}

.muntra-widget-button:hover {
  background-color: #0059c3;
  background-image: none;
  background-image: initial;
}
.secondary .muntra-widget-button {
  background-color: #fff;
  border-color: #52a8ff;
  color: #52a8ff;
}
.page-body {
  margin: 100px auto;
  max-width: 800px;
  padding-bottom: 200px;
  width: 100%;
}
.right-align {
  margin-right: 20px;
  text-align: right;
}
.muntra-widget.without-modal {
  width: 100%;
  height: 100%;
}
code {
  color: #c7254e;
  background-color: #f9f2f4;
  border-radius: 4px;
}
pre {
  display: block;
  margin: 0px 0 30px;
  color: #333;
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
  padding: 0.66001rem 9.5px 9.5px;
  line-height: 2rem;
  background: linear-gradient(
    to bottom,
    #fff 0,
    #fff 0.75rem,
    #f5f7fa 0.75rem,
    #f5f7fa 2.75rem,
    #fff 2.75rem,
    #fff 4rem
  );
  background-size: 100% 4rem;
  border-color: #d3daea;
}
pre code {
  padding: 0;
  font-size: inherit;
  color: inherit;
  white-space: pre-wrap;
  background-color: transparent;
  border-radius: 0;
}
.pre-scrollable {
  max-height: 340px;
  overflow-y: scroll;
}

@media (max-width: 992px) {
  .page-body {
    margin-top: 0px !important;
    max-width: 80vw;
  }
}

